import "./App.css";
import React from "react";
import Title from "./components/Title";
import AddNotification from "./components/AddNotification";
import { collection, query, getDocs, where } from "firebase/firestore";
import { db } from "./firebase";
import Noti from "./components/Noti";
import PlanA from "./images/PlanA.jpeg";
import PlanB_1 from "./images/PlanB_1.jpeg";
import PlanB_2 from "./images/PlanB_2.jpeg";
import PlanB_3 from "./images/PlanB_3.jpeg";
import PlanB_4 from "./images/PlanB_4.jpeg";
import PlanB_5 from "./images/PlanB_5.jpeg";
import PlanB_6 from "./images/PlanB_6.jpeg";

function App() {
  const [posts, setPosts] = React.useState([]);

  React.useEffect(() => {
    async function fetchData() {
      const xxx = collection(db, "notification");
      const q = query(
        xxx,
        where("date", ">", new Date(Date.now() - 7 * 24 * 60 * 60 * 1000)),
        where("date", "<=", new Date(Date.now() + 24 * 60 * 60 * 1000))
      );
      const querySnapshot = await getDocs(q);
      let todosArray = [];
      querySnapshot.forEach((doc) => {
        todosArray.push({ ...doc.data(), id: doc.id });
      });
      setPosts(todosArray);
    }
    fetchData();
  }, []);
  console.log(posts.length);
  return (
    <div className="App">
      <div>
        <img src={PlanA} alt="img" />
        <img src={PlanB_1} alt="img" />
        <img src={PlanB_2} alt="img" />
        <img src={PlanB_3} alt="img" />
        <img src={PlanB_4} alt="img" />
        <img src={PlanB_5} alt="img" />
        <img src={PlanB_6} alt="img" />
      </div>
      <div>
        <Title />
      </div>
      <div>
        <AddNotification />
      </div>
      {posts.length > 0 && (
        <div className="todo_container">
          {posts.map((todo) => (
            <Noti key={todo.id} todo={todo} />
          ))}
        </div>
      )}
    </div>
  );
}

export default App;
